/* global navigator */
import _snakeCase from 'lodash/snakeCase';

const env = process.env.NODE_ENV || 'development';
const isDevelopment = env === 'development';
const isStaging = env === 'staging';
const isProduction = env === 'production';

export default {
  companyName: 'Lobby',
  appName: process.env.REACT_APP_TITLE,
  appNamespace: _snakeCase(process.env.REACT_APP_TITLE),
  apiURL: process.env.REACT_APP_SERVER_URL,
  wsURL: process.env.REACT_APP_WS_URL,
  env,
  isDevelopment,
  isStaging,
  isProduction,
  livechatId: 12113223,
  slackClientId: process.env.REACT_APP_SLACK_CLIENT_ID || '',
  slackOAuthURL: 'https://slack.com/oauth/v2/authorize',
  slackOAuthScope: 'commands,users:read,users:read.email,chat:write',
  slackOAuthUserScope: 'users:read,users:read.email,chat:write',
  slackRedirectURL: `${process.env.REACT_APP_SERVER_URL}/lobby_portal/slack/integrate`,
  apiRequestTimeout: isProduction ? 12500 : 20000,
  mixpanelToken: process.env.REACT_APP_MIXPANEL,
  sentryDsn: process.env.SENTRY_DSN,
  locale: 'pt-br',
  isiOS: !!navigator.platform && /iPad|iPhone|iPod/.test(navigator.platform)
};
