// import digitalOceanUpload from './digital-ocean-upload';
import UploadsService from '../../services/uploads';

const uploadFile = ({
  file,
  progressHandler: progressHandlerProp
}) => {
  let resolve;
  let reject;
  const promise = new Promise((res, rej) => {
    resolve = res;
    reject = rej;
  });
  promise.cancelUpload = () => {
    promise.wasCanceled = true;
    reject(new Error('REQUEST CANCELED'));
  };
  const progressHandler = (args) => {
    if (promise.wasCanceled) return;
    progressHandlerProp({ ...file,
      ...args,
      cancelUpload: promise.cancelUpload,
      wasCanceled: promise.wasCanceled });
  };
  progressHandler({ progressPercent: 0 });

  const uploaderFunc = UploadsService.uploadToApi;

  uploaderFunc({ file, progressHandler })
    .then(resolve)
    .catch(reject);

  return promise;
};

export default uploadFile;
