import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  useMediaQuery,
  Dialog,
  DialogContent,
  DialogActions,
  DialogTitle,
  IconButton,
  Grid,
  Button
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { get as _get, mapValues as _mapValues, isEmpty as _isEmpty } from 'lodash';
import PropTypes from 'prop-types';
import Tracker from '../../../utils/tracker';
import TicketsCategoriesService from '../../../services/ticket-categories';
import FormTextField from '../../../components/FormFields/FormTextField';
import FormSelectField from '../../../components/FormFields/FormSelectField';
import styles from './styles';

const NewTicketCategoryModal = memo(({
  classes, onClose, setAlert, formData, setFormData, refetch,
  loading, setLoading, errors, setErrors, assignees
}) => {
  const disableNextButton = (loading || _isEmpty(formData.title));
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const updateFormData = (object) => {
    setFormData({ ...formData, ...object });
  };
  const onCreateTicketCategory = async () => {
    setLoading(true);
    try {
      await TicketsCategoriesService.createCategory(formData);
      Tracker.hit('TicketCategory - Created');
      setAlert({ alertMsg: 'Categoria criada com sucesso', alertType: 'success', showAlert: true });
      onClose();
      refetch(true);
    } catch (err) {
      setErrors(_mapValues(_get(err, 'errors', {}), (e) => e[0]));
      setAlert({ alertMsg: 'Erro ao criar categoria', alertType: 'error', showAlert: true });
    }
    setLoading(false);
  };
  return (
    <Dialog
      fullScreen={fullScreen}
      keepMounted
      fullWidth
      maxWidth="md"
      classes={{ paper: classes.dialogMemberForm }}
      open
    >
      <DialogTitle>
        Nova categoria
        <IconButton
          className={classes.closeButton}
          onClick={onClose}
          disabled={loading}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.content}>
        <Grid container direction="row" spacing={1}>
          <React.Fragment>
            <Grid item xs={12}>
              <FormTextField
                disabled={loading}
                label="Ttulo"
                errors={_get(errors, 'title', [])}
                value={_get(formData, 'title', '')}
                onChange={(e) => updateFormData({ title: e.target.value })}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormSelectField
                label="Responsvel"
                value={_get(formData, 'assigneeId', '')}
                disabled={loading}
                onChange={(e) => updateFormData({ assigneeId: e.target.value })}
                options={assignees.map((a) => ({ label: `${a.name} ${a.lastname}`, value: a.id }))}
                emptyOptionLabel="Nenhum responsvel"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormTextField
                label="Prazo de resoluo (em dias)"
                type="tel"
                disabled={loading}
                value={_get(formData, 'deadlineInDays', '')}
                onChange={(e) => updateFormData({ deadlineInDays: (e.target.value || '').replace(/[\D]+/g, '') })}
              />
            </Grid>
          </React.Fragment>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.actions}>
        <Grid container justify="space-between" spacing={2}>
          <Grid item xs={12} sm={8} />
          <Grid item xs={6} sm={2}>
            <Button
              fullWidth
              color="default"
              size="large"
              variant="contained"
              onClick={onClose}
              disabled={loading}
            >
              CANCELAR
            </Button>
          </Grid>
          <Grid item xs={6} sm={2}>
            <Button
              fullWidth
              color="primary"
              size="large"
              variant="contained"
              onClick={onCreateTicketCategory}
              disabled={disableNextButton}
            >
              SALVAR
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
});

NewTicketCategoryModal.propTypes = {
  classes: PropTypes.object,
  onClose: PropTypes.func,
  loading: PropTypes.bool,
  setLoading: PropTypes.func,
  errors: PropTypes.object,
  setErrors: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  setAlert: PropTypes.func,
  refetch: PropTypes.func,
  assignees: PropTypes.array
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('formData', 'setFormData', {}),
  withState('loading', 'setLoading', false),
  withState('errors', 'setErrors', {})
)(NewTicketCategoryModal);
