import React, { memo } from 'react';
import { compose, withState, withPropsOnChange, lifecycle } from 'recompose';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  useMediaQuery,
  Dialog,
  DialogContent,
  DialogActions,
  DialogTitle,
  IconButton,
  Grid,
  Switch,
  Button,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormHelperText,
  TextField
} from '@material-ui/core';
import PropTypes from 'prop-types';
import CloseIcon from '@material-ui/icons/Close';
import {
  get as _get, mapValues as _mapValues, isEmpty as _isEmpty,
  cloneDeep as _cloneDeep, map as _map, first as _first
} from 'lodash';
import TicketsCategoriesService from '../../../services/ticket-categories';
import FormTextField from '../../../components/FormFields/FormTextField';
import FormSelectField from '../../../components/FormFields/FormSelectField';
import Tracker from '../../../utils/tracker';
import { FIELDS_WITH_OPTIONS, FIELDS, TEXT_FIELD } from '../../../enums/fields';
import styles from './styles';

const NO_ASSIGNEE_LABEL = 'Nenhum responsvel';
const emptyQuestion = () => ({
  question: '',
  field_type: TEXT_FIELD,
  options: '',
  required: false
});

const TicketCategoryFormModal = memo(({
  classes, onClose, formData, setFormData, ticketCategory, setErrors,
  loading, errors, assignees, setLoading, setAlert, refetch, open
}) => {
  const disableNextButton = (loading || _isEmpty(formData.title));
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const formQuestions = _get(formData, 'formQuestions', []);
  const updateFormData = (object) => {
    setFormData({ ...formData, ...object });
  };
  const onAddFormQuestion = () => {
    formQuestions.push(emptyQuestion());
    updateFormData({ formQuestions: _cloneDeep(formQuestions) });
  };
  const onRemoveFormQuestion = (index) => {
    formQuestions.splice(index, 1);
    updateFormData({ formQuestions: _cloneDeep(formQuestions) });
  };
  const onUpdateFormQuestion = (field, value, index) => {
    formQuestions[index][field] = value;
    updateFormData({ formQuestions: _cloneDeep(formQuestions) });
  };
  const hasFormQuestionsError = () => {
    let hasError = false;
    updateFormData({
      formQuestions: _cloneDeep(formQuestions.map(f => {
        const questionError = _isEmpty(f.question);
        const optionsError = FIELDS_WITH_OPTIONS.includes(f.field_type) && _isEmpty(f.options);
        if (!hasError) {
          hasError = questionError || optionsError;
        }
        return {
          ...f,
          optionsError,
          questionError
        };
      }))
    });
    return hasError;
  };
  const onUpdateTicketCategory = async () => {
    if (!hasFormQuestionsError()) {
      setLoading(true);
      try {
        const body = { id: ticketCategory.id, ...formData };
        await TicketsCategoriesService.updateCategory(body);
        Tracker.hit('TicketCategoryUpdated - Success');
        setAlert({ alertMsg: 'Categoria atualizada com sucesso', alertType: 'success', showAlert: true });
        setLoading(false);
        await refetch(true);
        onClose();
      } catch (err) {
        setErrors(_mapValues(_get(err, 'errors', {}), e => _first(e)));
        setAlert({ alertMsg: 'Erro ao atualizar categoria', alertType: 'error', showAlert: true });
        setLoading(false);
      }
    }
  };
  if (open) {
    return (
      <Dialog
        fullScreen={fullScreen}
        keepMounted
        fullWidth
        maxWidth="sm"
        classes={{ paper: classes.dialogMemberForm }}
        open
      >
        <DialogTitle>
          Editar categoria
          <IconButton
            className={classes.closeButton}
            onClick={onClose}
            disabled={loading}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent className={classes.content}>
          <Grid container direction="row" spacing={1}>
            <Grid item xs={12}>
              <FormTextField
                disabled={loading}
                label="Ttulo"
                errors={_get(errors, 'title', [])}
                value={_get(formData, 'title', '')}
                onChange={e => updateFormData({ title: e.target.value })}
              />
            </Grid>
            <Grid item xs={7} sm={6}>
              <FormSelectField
                disabled={loading}
                errors={_get(errors, 'assignee_id', [])}
                label="Responsvel"
                value={_get(formData, 'assigneeId', '')}
                onChange={e => updateFormData({ assigneeId: e.target.value })}
                emptyOptionLabel={NO_ASSIGNEE_LABEL}
                options={assignees.map((a) => ({ label: `${a.name} ${a.lastname}`, value: a.id }))}
              />
            </Grid>
            <Grid item xs={5} sm={6}>
              <FormTextField
                type="tel"
                disabled={loading}
                label="Prazo de resoluo (em dias)"
                errors={_get(errors, 'deadline_in_days', [])}
                value={_get(formData, 'deadlineInDays', '')}
                onChange={e => updateFormData({
                  deadlineInDays: (e.target.value || '').replace(/[\D]+/g, '')
                })}
              />
            </Grid>
            <Grid item xs={12}>
              {
                _map(formQuestions, (fq, i) => (
                  <Grid
                    container
                    direction="row"
                    spacing={1}
                    key={i.toString()}
                  >
                    <Grid item xs={2} sm={1}>
                      <FormControl fullWidth margin="normal">
                        <Button
                          onClick={() => onRemoveFormQuestion(i)}
                          variant="contained"
                          disabled={loading}
                          className={classes.deleteVisitorButton}
                        >
                          -
                        </Button>
                      </FormControl>
                    </Grid>
                    <Grid item xs={10} sm={11}>
                      <FormControl fullWidth margin="normal" error={!!_get(fq, 'questionError')}>
                        <TextField
                          type="text"
                          disabled={loading}
                          label="Ttulo da pergunta"
                          value={_get(fq, 'question', '')}
                          error={!!_get(fq, 'questionError')}
                          InputLabelProps={{ shrink: true }}
                          onChange={e => onUpdateFormQuestion('question', e.target.value, i)}
                          variant="outlined"
                        />
                        {
                          _get(fq, 'questionError') &&
                            <FormHelperText>no pode ficar em branco</FormHelperText>
                        }
                      </FormControl>
                    </Grid>
                    <Grid item xs={2} sm={1} />
                    <Grid item xs={6} sm={8}>
                      <FormSelectField
                        disabled={loading}
                        label="Tipo do campo resposta"
                        value={_get(fq, 'field_type', '')}
                        onChange={e => onUpdateFormQuestion('field_type', e.target.value, i)}
                        options={FIELDS}
                        optionValueField="key"
                      />
                    </Grid>
                    <Grid item xs={4} sm={3}>
                      <FormGroup className={classes.toggleRequired}>
                        <FormControlLabel
                          control={
                            <Switch
                              color="primary"
                              disabled={loading}
                              checked={!!_get(fq, 'required')}
                              value=""
                              onChange={() => onUpdateFormQuestion('required', !_get(fq, 'required'), i)}
                            />
                          }
                          label={<small>Obrigatrio</small>}
                        />
                      </FormGroup>
                    </Grid>
                    <Grid item xs={2} sm={1} />
                    {
                      FIELDS_WITH_OPTIONS.includes(_get(fq, 'field_type')) &&
                        <Grid item xs={10} sm={11}>
                          <FormControl fullWidth margin="normal" error={!!_get(fq, 'optionsError')}>
                            <TextField
                              type="text"
                              disabled={loading}
                              label="Opes de respostas"
                              placeholder="Separe por vrgula, ex: Item1,Item2,Item3"
                              value={_get(fq, 'options', '')}
                              error={!!_get(fq, 'optionsError')}
                              InputLabelProps={{ shrink: true }}
                              onChange={e => onUpdateFormQuestion('options', e.target.value, i)}
                              variant="outlined"
                            />
                            {
                              _get(fq, 'optionsError') &&
                                <FormHelperText>no pode ficar em branco</FormHelperText>
                            }
                          </FormControl>
                        </Grid>
                    }
                  </Grid>
                ))
              }
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions className={classes.actions}>
          <Grid container justify="space-between" spacing={2}>
            <Grid item xs={12} sm={6}>
              <Button
                fullWidth={fullScreen}
                color="primary"
                size="large"
                variant="outlined"
                disabled={loading}
                onClick={onAddFormQuestion}
              >
                ADICIONAR PERGUNTA
              </Button>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Button
                fullWidth
                color="default"
                size="large"
                variant="contained"
                onClick={onClose}
                disabled={loading}
              >
                CANCELAR
              </Button>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Button
                fullWidth
                color="primary"
                size="large"
                variant="contained"
                onClick={onUpdateTicketCategory}
                disabled={disableNextButton}
              >
                SALVAR
              </Button>
            </Grid>
          </Grid>
        </DialogActions>
      </Dialog>
    );
  }
  return null;
});

TicketCategoryFormModal.propTypes = {
  classes: PropTypes.object,
  onClose: PropTypes.func,
  loading: PropTypes.bool,
  open: PropTypes.bool,
  errors: PropTypes.object,
  setErrors: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  ticketCategory: PropTypes.object,
  assignees: PropTypes.array,
  setLoading: PropTypes.func,
  setAlert: PropTypes.func,
  refetch: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('formData', 'setFormData', {}),
  withState('loading', 'setLoading', false),
  withState('errors', 'setErrors', {}),
  withPropsOnChange(['open'], ({ open, setFormData, ticketCategory }) => {
    if (open) {
      Tracker.hit('TicketCategory - Opened');
      setFormData({
        title: _get(ticketCategory, 'title', ''),
        assigneeId: _get(ticketCategory, 'assignee_id', ''),
        deadlineInDays: _get(ticketCategory, 'deadline_in_days', ''),
        formQuestions: _get(ticketCategory, 'ticket_form_questions', [])
      });
    } else {
      Tracker.hit('TicketCategory - Closed');
    }
  }),
  lifecycle({
    componentDidMount() {
      const { setFormData, ticketCategory } = this.props;
      setFormData({
        title: _get(ticketCategory, 'title', ''),
        assigneeId: _get(ticketCategory, 'assignee_id', ''),
        deadlineInDays: _get(ticketCategory, 'deadline_in_days', ''),
        formQuestions: _get(ticketCategory, 'ticket_form_questions', [])
      });
    }
  })
)(TicketCategoryFormModal);
