import React, { memo } from 'react';
import { get as _get, size as _size } from 'lodash';
import { compose, withState } from 'recompose';
import PropTypes from 'prop-types';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper,
  Button,
  Typography
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import withRequest from '../../../utils/with-request';
import withNotAuthorizedHandler from '../../../utils/with-not-authorized-handler';
import TicketCategoriesService from '../../../services/ticket-categories';
import TicketsService from '../../../services/tickets';
import { USER_MANAGER_ROLES } from '../../../enums/user-roles';
import Loading from '../../../components/Loading';
import TicketCategoriesItemRow from '../TicketCategoriesItemRow';
import Session from '../../../utils/session';
import Tracker from '../../../utils/tracker';
import NewTicketCategoryModal from '../NewTicketCategoryModal';
import styles from './styles';

const TicketCategoriesTable = memo(({
  ticketCategoriesData, ticketCategoriesLoading, openTicketCategoryModal,
  setOpenTicketCategoryModal, classes, setAlert, ticketCategoriesFetch,
  ticketAssigneesData, ticketAssigneesLoading
}) => {
  const viewer = Session.getUserData();
  const isManager = USER_MANAGER_ROLES.includes(_get(viewer, 'member.role'));
  const isLoading = ticketAssigneesLoading || ticketCategoriesLoading;
  const ticketCategories = _get(ticketCategoriesData, 'ticket_categories');
  const onOpenNewTicketCategory = () => {
    Tracker.hit('TicketCategory - Opened');
    setOpenTicketCategoryModal(true);
  };
  const onClose = () => {
    Tracker.hit('TicketCategory - Closed');
    setOpenTicketCategoryModal(false);
  };
  const assignees = _get(ticketAssigneesData, 'members', []);
  return (
    <React.Fragment>
      <div className={classes.container}>
        <div className={classes.headerView}>
          <aside>
            <Typography variant="h6">
              <b>{`Categorias (${_size(ticketCategories)})`}</b>
            </Typography>
          </aside>
          <aside>
            {
              isManager &&
                <Button
                  size="large"
                  color="primary"
                  variant="contained"
                  onClick={onOpenNewTicketCategory}
                >
                  Nova Categoria
                </Button>
            }
          </aside>
        </div>
      </div>
      <div className={classes.container}>
        <Paper className={classes.tablePaper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell className={classes.tableCell}>TTULO</TableCell>
                <TableCell className={classes.tableCell}>RESPONSVEL</TableCell>
                <TableCell className={classes.tableCell}>PRAZO DE RESOLUO</TableCell>
                {isManager && <TableCell className={classes.tableCell}>HABILITAR</TableCell>}
                <TableCell colSpan={1} />
              </TableRow>
            </TableHead>
            <TableBody>
              {
                isLoading ?
                  <TableRow>
                    <TableCell colSpan={isManager ? 5 : 4} align="center">
                      <Loading />
                    </TableCell>
                  </TableRow>
                  :
                  (
                    _size(ticketCategories) ?
                      (ticketCategories.map((tc, i) => {
                        return (
                          <TicketCategoriesItemRow
                            isManager={isManager}
                            assignees={assignees}
                            setAlert={setAlert}
                            index={i}
                            key={tc.id}
                            ticketCategory={tc}
                            isLoading={isLoading}
                            refetch={ticketCategoriesFetch}
                          />
                        );
                      }))
                      :
                      <TableRow>
                        <TableCell className={classes.empty} colSpan={isManager ? 5 : 4} align="center">
                          Nenhuma categoria cadastrada
                        </TableCell>
                      </TableRow>
                  )
              }
              {
                isManager && openTicketCategoryModal &&
                  <NewTicketCategoryModal
                    assignees={assignees}
                    onClose={onClose}
                    setAlert={setAlert}
                    refetch={ticketCategoriesFetch}
                  />
              }
            </TableBody>
          </Table>
        </Paper>
      </div>
    </React.Fragment>
  );
});

TicketCategoriesTable.propTypes = {
  ticketCategoriesLoading: PropTypes.bool,
  ticketCategoriesData: PropTypes.object,
  classes: PropTypes.object,
  openTicketCategoryModal: PropTypes.bool,
  setOpenTicketCategoryModal: PropTypes.func,
  ticketCategoriesFetch: PropTypes.func,
  setAlert: PropTypes.func,
  ticketAssigneesData: PropTypes.object,
  ticketAssigneesLoading: PropTypes.bool
};

const withTicketCategories = withRequest(TicketCategoriesService.getCategories, {
  name: 'ticketCategories',
  options: ({ setAlert }) => ({
    variables: true,
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao carregar dados do servidor', alertType: 'error', showAlert: true });
    }
  })
});

const withTicketAssignees = withRequest(TicketsService.getAssignables, {
  name: 'ticketAssignees'
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('openTicketCategoryModal', 'setOpenTicketCategoryModal', false),
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.member.role)
  }),
  withTicketCategories,
  withTicketAssignees
)(TicketCategoriesTable);
