import React, { memo } from 'react';
import { compose, withState, lifecycle } from 'recompose';
import PropTypes from 'prop-types';
import { get as _get, isEmpty as _isEmpty } from 'lodash';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Switch from '@material-ui/core/Switch';
import AvatarImage from '../../../components/AvatarImage';
import withViewerData from '../../../utils/with-viewer-data';
import Tracker from '../../../utils/tracker';
import TicketCategoryFormModal from '../TicketCategoryFormModal';
import TicketsCategoriesService from '../../../services/ticket-categories';
import styles from './styles';

const EditButtonBase = ({
  ticketCategory, ticketCategoryLoading, refetch,
  openModal, setOpenModal, setAlert, assignees
}) => {
  const onClick = () => {
    Tracker.hit('TicketCategoryModal - Opened');
    setOpenModal(true);
  };
  const onClose = async () => {
    Tracker.hit('TicketCategoryModal - Closed');
    setOpenModal(false);
  };
  return (
    <React.Fragment>
      <Button
        variant="outlined"
        size="large"
        onClick={onClick}
      >
        Editar
      </Button>
      <TicketCategoryFormModal
        assignees={assignees}
        isLoading={ticketCategoryLoading}
        ticketCategory={ticketCategory}
        open={openModal}
        onClose={onClose}
        setAlert={setAlert}
        refetch={refetch}
      />
    </React.Fragment>
  );
};

EditButtonBase.propTypes = {
  ticketCategory: PropTypes.object,
  ticketCategoryLoading: PropTypes.bool,
  openModal: PropTypes.bool,
  setOpenModal: PropTypes.func,
  setAlert: PropTypes.func,
  assignees: PropTypes.array,
  refetch: PropTypes.func
};

const EditButton = compose(
  withState('openModal', 'setOpenModal', false)
)(EditButtonBase);

const TicketCategoriesItemRow = memo(({
  viewer, index, classes, setAlert, ticketCategory, loading, setLoading,
  assignees, refetch, isManager, setChecked, checked
}) => {
  const onToggle = async () => {
    setLoading(true);
    try {
      await TicketsCategoriesService.toggleCategory(ticketCategory.id);
      setChecked(!checked);
    } catch {
      setAlert({ alertMsg: 'Erro ao alterar categoria do ticket', alertType: 'error', showAlert: true });
    }
    setLoading(false);
  };
  const assignee = _get(ticketCategory, 'assignee');
  const deadlineInDays = _get(ticketCategory, 'deadline_in_days');
  let assigneeFullName = '';
  let assigneePhotoURL = '';
  if (assignee) {
    assigneeFullName = `${assignee.name} ${assignee.lastname}`;
    assigneePhotoURL = assignee.photoUrl;
  }
  if (!viewer) return null;
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{_get(ticketCategory, 'title')}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <div className={classes.avatarNameContainer}>
          {
            assignee &&
              <AvatarImage
                name={assigneeFullName}
                photoUrl={assigneePhotoURL}
              />
          }
          <span className={`${classes.cellText} ${classes.userName}`}>
            {assigneeFullName}
          </span>
        </div>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>
          {
            deadlineInDays ?
              `${deadlineInDays} ${deadlineInDays > 1 ? 'dias teis' : 'dia til'}`
              :
              ''
          }
        </span>
      </TableCell>
      <TableCell className={classes.tableExtraSmallCell}>
        {
          isManager &&
            <Switch
              disabled={loading}
              checked={checked}
              onChange={onToggle}
              color="primary"
            />
        }
      </TableCell>
      <TableCell className={classes.tableMediumCell}>
        {
          isManager &&
            <EditButton
              assignees={assignees}
              ticketCategory={ticketCategory}
              setAlert={setAlert}
              refetch={refetch}
            />
        }
      </TableCell>
    </TableRow>
  );
});

TicketCategoriesItemRow.propTypes = {
  index: PropTypes.number,
  ticketCategory: PropTypes.object,
  viewer: PropTypes.object,
  classes: PropTypes.object,
  setAlert: PropTypes.func,
  assignees: PropTypes.array,
  refetch: PropTypes.func,
  isManager: PropTypes.bool,
  setChecked: PropTypes.func,
  checked: PropTypes.bool,
  setLoading: PropTypes.func,
  loading: PropTypes.bool
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('checked', 'setChecked', false),
  withState('loading', 'setLoading', false),
  withViewerData,
  lifecycle({
    componentDidMount() {
      const { setChecked, ticketCategory } = this.props;
      setChecked(_isEmpty(ticketCategory.deleted_at));
    }
  })
)(TicketCategoriesItemRow);
