import _map from 'lodash/map';
import _split from 'lodash/split';
import _compact from 'lodash/compact';
import _get from 'lodash/get';
import _trim from 'lodash/trim';
import Api from '../utils/api';

const TicketCategories = {
  getCategories(includeDeleteds) {
    let body = {};
    if (includeDeleteds) {
      body = { deleted: includeDeleteds };
    }
    return Api.get('/ticket_categories', body);
  },
  updateCategory({ id, title, assigneeId, deadlineInDays, formQuestions }) {
    const body = {
      id,
      title,
      assignee_id: assigneeId,
      deadline_in_days: deadlineInDays,
      ticket_form_questions: _map(formQuestions, f => ({
        question: _get(f, 'question'),
        field_type: _get(f, 'field_type'),
        required: _get(f, 'required'),
        options: _compact(_split(_get(f, 'options', ''), ',').map(o => _trim(o)))
      }))
    };
    return Api.put(`/ticket_categories/${id}`, body);
  },
  createCategory({ title, deadlineInDays, assigneeId }) {
    const body = { title, deadline_in_days: deadlineInDays, assignee_id: assigneeId };
    return Api.post('/ticket_categories', body);
  },
  toggleCategory(id) {
    return Api.put(`/ticket_categories/${id}/toggle_active`);
  },
  getCategoriesByCompany(companyId) {
    if (companyId) {
      return Api.get('/public_ticket_categories', { company_id: companyId });
    }
    return null;
  }
};

export default TicketCategories;
