import React, { memo } from 'react';
import { get as _get } from 'lodash';
import { compose, withState } from 'recompose';
import { Box, Button, IconButton, Menu, MenuItem } from '@material-ui/core';
import { MoreVert as MoreVertIcon } from '@material-ui/icons';
import PropTypes from 'prop-types';

import Tracker from '../../../utils/tracker';
import withRequest from '../../../utils/with-request';
import TicketsService from '../../../services/tickets';
import withFormData from '../../../utils/with-form-data';
import StandardDialog from '../../../components/StandardDialog';

import ExportTicketsCsvForm from './ExportTicketsCsvForm';

const ExportTicketsCsvModalBase = ({
  isModalOpen, onClose: onCloseArg,
  formData, setFormData, resetFormData, exportCsvFetch, exportCsvLoading
}) => {
  const onSubmit = () => exportCsvFetch(true);
  const onClose = () => {
    resetFormData();
    setTimeout(onCloseArg, 10);
  };

  return (
    isModalOpen && (
      <StandardDialog
        open={isModalOpen}
        onClose={onClose}
        modalTitle="Exportar todos os tickets"
        content={
          <ExportTicketsCsvForm
            formData={formData}
            setFormData={setFormData}
            isLoading={exportCsvLoading}
          />
        }
        actions={
          <React.Fragment>
            <Button
              color="default"
              size="large"
              variant="contained"
              onClick={onClose}
              disabled={exportCsvLoading}
            >
              CANCELAR
            </Button>
            <Box ml={1}>
              <Button
                color="primary"
                size="large"
                variant="contained"
                onClick={onSubmit}
                disabled={exportCsvLoading}
              >
                EXPORTAR
              </Button>
            </Box>
          </React.Fragment>
        }
      />
    )
  );
};

ExportTicketsCsvModalBase.propTypes = {
  isModalOpen: PropTypes.bool,
  onClose: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  resetFormData: PropTypes.func,
  exportCsvFetch: PropTypes.func,
  exportCsvLoading: PropTypes.bool
};

const withExportCsv = withRequest(TicketsService.exportCsv, {
  name: 'exportCsv',
  init: false,
  options: ({ formData, setFormData, setAlert, onClose }) => {
    const { validationErrors, ...formFields } = formData;

    return {
      variables: formFields,
      onRequestFinished: (data) => {
        setAlert({
          alertMsg: _get(data, 'flash_message.message'), alertType: 'success', showAlert: true
        });
        onClose();
      },
      onRequestError: error => {
        setAlert({
          alertMsg: 'Erro ao tentar exportar CSV', alertType: 'error', showAlert: true
        });
        setFormData({ validationErrors: _get(error, 'errors', {}) });
      }
    };
  }
});

const ExportTicketsCsvModal = compose(
  memo,
  withFormData(() => ({
    start_date: null,
    end_date: null
  })),
  withExportCsv
)(ExportTicketsCsvModalBase);

const ITEM_HEIGHT = 48;

const TicketsMenuButtonBase = ({
  setAlert, anchorEl, setAnchorEl, isExportModalOpen, setIsExportModalOpen,
  isArchived, onToggleArchived
}) => {
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const onExportTicketsClick = () => {
    Tracker.hit('ExportTicketsCsvModal - Opened');
    setIsExportModalOpen(true);
    handleClose();
  };
  const onExportTicketsClose = () => {
    Tracker.hit('ExportTicketsCsvModal - Closed');
    setIsExportModalOpen(false);
  };
  const onToggleArchivedClick = () => {
    const archivedState = !isArchived;
    Tracker.hit(`Tickets${archivedState ? 'Archived' : ''} - Viewer`);
    setAnchorEl(null);
    onToggleArchived(archivedState);
  };
  return (
    <div>
      <IconButton
        aria-label="more"
        aria-controls="long-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: {
            maxHeight: ITEM_HEIGHT * 4.5,
            width: '23ch'
          }
        }}
      >
        <MenuItem onClick={onToggleArchivedClick}>
          {isArchived ? 'Ver no arquivados' : 'Ver arquivados'}
        </MenuItem>
        <MenuItem onClick={onExportTicketsClick}>
          Exportar Tickets
          <ExportTicketsCsvModal
            setAlert={setAlert}
            isModalOpen={isExportModalOpen}
            onClose={onExportTicketsClose}
          />
        </MenuItem>
      </Menu>
    </div>
  );
};
TicketsMenuButtonBase.propTypes = {
  setAlert: PropTypes.func,
  anchorEl: PropTypes.object,
  setAnchorEl: PropTypes.func,
  isArchived: PropTypes.bool,
  onToggleArchived: PropTypes.func,
  isExportModalOpen: PropTypes.bool,
  setIsExportModalOpen: PropTypes.func
};

const TicketsMenuButton = compose(
  memo,
  withState('anchorEl', 'setAnchorEl', null),
  withState('isExportModalOpen', 'setIsExportModalOpen', false)
)(TicketsMenuButtonBase);

export default TicketsMenuButton;
