import React, { memo } from 'react';
import { merge as _merge } from 'lodash';
import PropTypes from 'prop-types';

import uploadFile from '../../utils/upload-files';
import AttachButton from '../AttachButton';
import FilePreviewButton from '../FilePreviewButton';

const DescriptionAttachButton = memo(({
  value, onChange, disabled,
  onUploadProgress, onUploadSuccess, onUploadError
}) => {
  const onFileChange = (newFile) => {
    uploadFile({
      file: newFile,
      progressHandler: ({ progressPercent }) => {
        const updatedFile = _merge(
          newFile, { progressPercent }
        );
        onChange(updatedFile);
        if (onUploadProgress) onUploadProgress(updatedFile);
      }
    })
      .catch(onUploadError)
      .then(result => {
        const updatedFile = _merge(newFile, result);
        onChange(updatedFile);
        if (onUploadSuccess) onUploadSuccess(updatedFile);
      });
  };

  if (!value || (!value.progressPercent && !value.id)) {
    return (
      <AttachButton
        accept="image/*,.doc,.docx,.xls,.xlsx,.pdf,.xml"
        onChange={onFileChange}
        disabled={disabled}
      />
    );
  }

  return (
    <FilePreviewButton
      file={value}
      onRemoveFile={fileToRemove => fileToRemove.cancelUpload || onChange(null)}
      disabled={disabled}
    />
  );
});

DescriptionAttachButton.propTypes = {
  value: PropTypes.object,
  onChange: PropTypes.func,
  disabled: PropTypes.bool,
  onUploadProgress: PropTypes.func,
  onUploadSuccess: PropTypes.func,
  onUploadError: PropTypes.func
};

export default DescriptionAttachButton;
