import React, { memo } from 'react';
import { compose, withState } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';

import Header from '../../../../components/Header';
import SettingsMenu from '../../../../components/SettingsMenu';
import AlertBox from '../../../../components/AlertBox';
import styles from './styles';

const WrapperBase = memo(({
  classes, alert, setAlert, WrappedComponent
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }

  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu
            setAlert={setAlert}
          />
        </div>
        <div className={classes.container}>
          <WrappedComponent setAlert={setAlert} />
        </div>
      </section>
    </div>
  );
});

WrapperBase.propTypes = {
  WrappedComponent: PropTypes.func,
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const Wrapper = compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  })
)(WrapperBase);

const withWrapper = WrappedComponent => props => (
  <Wrapper {...props} WrappedComponent={WrappedComponent} />
);

export default withWrapper;
