import _get from 'lodash/get';
import Api from '../utils/api';

const Tickets = {
  getTickets(params) {
    return Api.get('/tickets', {
      location_id: _get(params, 'locationId'),
      assignee_id: _get(params, 'assigneeId'),
      category_id: _get(params, 'categoryId'),
      deadline: _get(params, 'deadline'),
      priority: _get(params, 'priority'),
      archived: _get(params, 'archived')
    });
  },
  getTicket(ticketId) {
    if (ticketId) {
      return Api.get(`/tickets/${ticketId}`);
    }
    return null;
  },
  getTicketByCode(code) {
    if (code) {
      return Api.get(`/tickets/codes/${code}`);
    }
    return null;
  },
  updateTicket({ ticket, selectedAssignees }) {
    const body = {
      id: _get(ticket, 'id'),
      title: _get(ticket, 'title'),
      priority: _get(ticket, 'priority'),
      deadline: _get(ticket, 'deadline'),
      location_id: _get(ticket, 'location.id'),
      assignee_ids: selectedAssignees,
      category_id: _get(ticket, 'category.id')
    };
    return Api.put(`/tickets/${_get(ticket, 'id')}`, body);
  },
  moveTicket({ id, status, position, location_id }) {
    return Api.put(`/tickets/${id}/move`, { status, position, location_id });
  },
  archiveTicket({ ticketsIds }) {
    return Api.put('/tickets/archive', { tickets_ids: ticketsIds });
  },
  postTicket(ticket) {
    return Api.post('/tickets', ticket);
  },
  getAssignables() {
    return Api.get('/tickets/assignables');
  },
  exportCsv({ start_date, end_date }) {
    return Api.post('/tickets/export_csv', { start_date, end_date });
  },
  markAsRead(ticketId) {
    return Api.put(`/tickets/${ticketId}/mark_as_read`);
  },
  postPublicTicket({ ticket, companyId }) {
    return Api.post(`/public_tickets?company_id=${companyId}`, ticket);
  }
};

export default Tickets;
