import React, { memo } from 'react';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import { Link, withRouter } from 'react-router-dom';
import { get as _get, find as _find } from 'lodash';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import moment from 'moment';
import ServiceRequestEnums from '../../../enums/service-request';
import withViewerData from '../../../utils/with-viewer-data';
import styles from './styles';

const { SERVICE_REQUEST_STATUSES } = ServiceRequestEnums;
const MyRequestsItemRow = memo(({
  viewer, classes, serviceRequest
}) => {
  if (!viewer) return null;
  const serviceTitle = _get(serviceRequest, 'service.title', '');
  const serviceRequestDate = _get(serviceRequest, 'created_at', '');
  const serviceImage = _get(serviceRequest, 'service.image_url', '');
  const serviceRequestStatus = _get(serviceRequest, 'status', '');
  let serviceRequestStatusClassName = '';
  switch (serviceRequestStatus) {
  case 'IN_PROGRESS':
    serviceRequestStatusClassName = 'statusContainerInProgress';
    break;
  case 'CANCELED':
    serviceRequestStatusClassName = 'statusContainerCanceled';
    break;
  case 'DONE':
    serviceRequestStatusClassName = 'statusContainerDone';
    break;
  default:
  }
  const serviceRequestStatusTranslated = _find(
    SERVICE_REQUEST_STATUSES, { value: serviceRequestStatus }
  ).label;

  return (
    <TableRow>
      <TableCell>
        <div className={classes.container}>
          <img
            src={serviceImage}
            alt={serviceTitle}
            className={classes.image}
          />
          <div className={classes.InnerContainerTexts}>
            <p className={classes.title}><b>{serviceTitle}</b></p>
            <p className={classes.subtitle}>{`Enviado em ${moment(serviceRequestDate).format('DD/MM/YYYY')}`}</p>
          </div>
        </div>
      </TableCell>
      <TableCell>
        <span className={_get(classes, serviceRequestStatusClassName, '')}>
          <b>{serviceRequestStatusTranslated}</b>
        </span>
      </TableCell>
      <TableCell>
        <div className={classes.buttonContainer}>
          <Button
            variant="outlined"
            component={Link}
            to={`/meus-pedidos/${serviceRequest.id}`}
            color="primary"
            size="large"
          >
            Ver Solicitao
          </Button>
        </div>
      </TableCell>
    </TableRow>
  );
});

MyRequestsItemRow.propTypes = {
  viewer: PropTypes.object,
  classes: PropTypes.object,
  serviceRequest: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withViewerData
)(MyRequestsItemRow);
