import React, { memo } from 'react';
import { size as _size, map as _map } from 'lodash';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper
} from '@material-ui/core';
import { USER_MANAGER_ROLES } from '../../../enums/user-roles';
import withNotAuthorizedHandler from '../../../utils/with-not-authorized-handler';
import Loading from '../../../components/Loading';
import MyRequestsItemRow from '../MyRequestsItemRow';

const MyRequestsTable = memo(({ serviceRequests, serviceRequestsLoading }) => {
  const loading = serviceRequestsLoading;
  return (
    <React.Fragment>
      <div>
        <Paper>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>SERVIO</TableCell>
                <TableCell>STATUS</TableCell>
                <TableCell colSpan={1} />
              </TableRow>
            </TableHead>
            <TableBody>
              {
                loading ?
                  <TableRow>
                    <TableCell colSpan={3} align="center">
                      <Loading />
                    </TableCell>
                  </TableRow>
                  :
                  (
                    _size(serviceRequests) ?
                      _map(serviceRequests, (serviceRequest, i) => {
                        return (
                          <MyRequestsItemRow
                            serviceRequest={serviceRequest}
                            index={i}
                            key={serviceRequest.id}
                          />
                        );
                      })
                      :
                      <TableRow>
                        <TableCell colSpan={5} align="center">
                          Nenhum servio requisitado
                        </TableCell>
                      </TableRow>
                  )
              }
            </TableBody>
          </Table>
        </Paper>
      </div>
    </React.Fragment>
  );
});

MyRequestsTable.propTypes = {
  serviceRequests: PropTypes.array,
  serviceRequestsLoading: PropTypes.bool
};

export default compose(
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.member.role)
  })
)(MyRequestsTable);
