import React, { memo } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import _get from 'lodash/get';
import _size from 'lodash/size';
import CloseIcon from '@material-ui/icons/Close';
import { TableCell, TableRow, Grid } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';

const STATUS = {
  ACCEPTED: 'Autorizado',
  PENDING: 'Pendente',
  CANCELED: 'Cancelado'
};

const VisitorsItemRow = memo(({
  classes, index, visitor, isSmallScreen, fields, onCancelVisitor
}) => {
  const visitorId = _get(visitor, 'id');
  const visitorFullName = `${_get(visitor, 'name')} ${_get(visitor, 'visitor_company') || ''}`;
  const hostMemberFullName = `${_get(visitor, 'host_member.name')} ${_get(visitor, 'host_member.lastname')}`;
  const hostMemberEmail = _get(visitor, 'host_member.email');
  const inviterFullName = `${_get(visitor, 'inviter.name')} ${_get(visitor, 'inviter.lastname')}`;
  const inviterEmail = _get(visitor, 'inviter.email');
  const visitDate = _get(visitor, 'visit_date');
  const visitDateFormat = visitDate ? `${moment(visitDate).format('DD/MM/YYYY')}` : '--';
  const visitHour = moment(_get(visitor, 'visit_hour'));
  const visitHourFormat = visitHour ? `${moment(visitHour).format('HH:mm')}` : '--';
  const checkinAt = _get(visitor, 'checkin_at');
  const checkinAtFormat = checkinAt ? moment(checkinAt).format('DD/MM/YY HH:mm') : '';
  const visitPeriod = moment(visitDate).hour(visitHour.hour()).minute(visitHour.minute());
  const canCancel = visitor.status !== 'ACCEPTED' && moment(visitPeriod).isAfter(moment().startOf('day'));
  if (isSmallScreen) {
    const fieldData = [
      `${visitDateFormat} ${visitHourFormat}`,
      `${STATUS[visitor.status]}${checkinAtFormat ? ` s ${checkinAtFormat}` : ''}`,
      visitorFullName,
      `${hostMemberFullName} (${inviterEmail})`,
      `${inviterFullName} (${inviterEmail})`
    ];
    const labelFields = fields.slice(0, -1);
    return (
      <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
        <TableCell colSpan={_size(fields)}>
          <Grid container spacing={3}>
            <Grid item xs={10}>
              {
                labelFields.map((f, fIndex) => (
                  <div key={fIndex.toString()}>
                    <strong>{f}</strong>
                    <br />
                    <span>{fieldData[fIndex]}</span>
                  </div>
                ))
              }
            </Grid>
            <Grid item xs={2}>
              {
                canCancel &&
                  <button
                    type="button"
                    className={classes.clearButton}
                    onClick={() => onCancelVisitor(visitorId)}
                  >
                    <CloseIcon />
                  </button>
              }
            </Grid>
          </Grid>
        </TableCell>
      </TableRow>
    );
  }
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableCell}>
        {visitDateFormat}
        <br />
        {visitHourFormat}
      </TableCell>
      <TableCell className={classes.tableCell}>
        {STATUS[visitor.status]}
        <br />
        {checkinAtFormat}
      </TableCell>
      <TableCell className={classes.tableCell}>
        {visitorFullName}
      </TableCell>
      <TableCell className={classes.tableCell}>
        {hostMemberFullName}
        <br />
        <small>{hostMemberEmail}</small>
      </TableCell>
      <TableCell className={classes.tableCell}>
        {inviterFullName}
        <br />
        <small>{inviterEmail}</small>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        {
          canCancel &&
            <button
              type="button"
              className={classes.clearButton}
              onClick={() => onCancelVisitor(visitorId)}
            >
              <CloseIcon />
            </button>
        }
      </TableCell>
    </TableRow>
  );
});

VisitorsItemRow.propTypes = {
  index: PropTypes.number,
  visitor: PropTypes.object,
  classes: PropTypes.object,
  onCancelVisitor: PropTypes.func,
  fields: PropTypes.array,
  isSmallScreen: PropTypes.bool
};

export default withStyles(styles, { withTheme: true })(VisitorsItemRow);
