import React from 'react';
import { get as _get } from 'lodash';
import PropTypes from 'prop-types';

import FormTextField from './FormTextField';

const FormSelectField = ({
  label, options, errors, emptyOptionLabel = false, readOnly,
  optionValueField = 'value', optionLabelField = 'label',
  SelectProps = { native: true }, ...otherProps
}) => (
  readOnly ?
    <FormTextField
      errors={errors}
      label={label}
      {...otherProps}
    />
    :
    <FormTextField
      select
      errors={errors}
      SelectProps={SelectProps}
      label={label}
      InputProps={{ readOnly }}
      {...otherProps}
    >
      {emptyOptionLabel ? <option value={null}>{emptyOptionLabel}</option> : ''}
      {options.map(r => (
        <option value={_get(r, optionValueField)} key={_get(r, optionValueField)}>
          {_get(r, optionLabelField)}
        </option>))}
    </FormTextField>
);

FormSelectField.propTypes = {
  label: PropTypes.string,
  options: PropTypes.array,
  SelectProps: PropTypes.shape({
    native: PropTypes.bool
  }),
  readOnly: PropTypes.bool,
  errors: PropTypes.arrayOf(PropTypes.string),
  emptyOptionLabel: PropTypes.bool,
  optionValueField: PropTypes.string,
  optionLabelField: PropTypes.string
};

export default FormSelectField;
