import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _size from 'lodash/size';
import { withStyles, useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Loading from '../../../components/Loading';
import VisitorsItemRow from '../VisitorsItemRow';
import styles from './styles';

const FIELDS = ['Data visita', 'Status', 'Visitante', 'Visita para', 'Criado por', 'Ao'];

const VisitorsTable = memo(({ classes, visitors, loading, onCancelVisitor }) => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const colSpan = isSmallScreen ? 1 : _size(FIELDS);
  return (
    <div className={classes.container}>
      <Paper className={classes.paper}>
        <Table>
          <TableHead>
            <TableRow>
              {
                isSmallScreen ?
                  <TableCell className={classes.tableCell}>VISITAS REGISTRADAS</TableCell>
                  :
                  FIELDS.map((f, fIndex) => (
                    <TableCell className={classes.tableCell} key={fIndex.toString()}>
                      {f.toUpperCase()}
                    </TableCell>
                  ))
              }
            </TableRow>
          </TableHead>
          <TableBody>
            {
              loading ?
                <TableRow>
                  <TableCell colSpan={colSpan} align="center">
                    <Loading />
                  </TableCell>
                </TableRow>
                :
                (
                  _size(visitors) ?
                    (visitors.map((visitor, i) => {
                      return (
                        <VisitorsItemRow
                          index={i}
                          key={visitor.id}
                          visitor={visitor}
                          loading={loading}
                          onCancelVisitor={onCancelVisitor}
                          fields={FIELDS}
                          isSmallScreen={isSmallScreen}
                        />
                      );
                    }))
                    :
                    <TableRow>
                      <TableCell className={classes.empty} colSpan={colSpan} align="center">
                        Nenhum visitante registrado
                      </TableCell>
                    </TableRow>
                )
            }
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
});

VisitorsTable.propTypes = {
  visitors: PropTypes.array,
  loading: PropTypes.bool,
  classes: PropTypes.object,
  onCancelVisitor: PropTypes.func
};

export default withStyles(styles, { withTheme: true })(VisitorsTable);
