import React, { memo } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import _get from 'lodash/get';
import _lowerFirst from 'lodash/lowerFirst';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import AvatarImage from '../../../../components/AvatarImage';
import FilePreviewButton from '../../../../components/FilePreviewButton';
import styles from './styles';

const TicketComment = memo(({ comment, classes }) => {
  const isInternal = _get(comment, 'is_internal');
  const isInternalStyleClass = isInternal ? classes.internalBallon : '';
  const jobRole = _get(comment, 'member.job_role') ? `- ${_get(comment, 'member.job_role')}` : '';
  const fullName = `${_get(comment, 'member.name', '')} ${_get(comment, 'member.lastname', '')}`;
  const isActivity = _get(comment, 'is_activity');
  const content = _lowerFirst(comment.content || '');
  const isInternalLabel = isInternal && !isActivity ? ' - Interno' : '';
  const activityOrJobRole = isActivity ? `${content}.` : jobRole;
  return (
    <div>
      <div className={classes.userNameContainer}>
        <AvatarImage name={fullName} photoUrl={_get(comment, 'member.photo_url', '')} />
        <div>
          <Typography variant="subtitle2" className={classes.userName}>
            <b>{fullName}</b>
            &nbsp;
            <Typography variant="body2" component="span">{activityOrJobRole}</Typography>
          </Typography>
          {
            isActivity && (
              <Typography variant="body2" color="textSecondary" component="small" className={classes.userName}>
                {`${moment(_get(comment, 'created_at')).calendar()}${isInternalLabel}`}
              </Typography>
            )
          }
        </div>
      </div>
      <div className={classes.content}>
        {
          !isActivity && (
            <React.Fragment>
              {
                comment.attachment_data && (
                  <FilePreviewButton
                    file={comment.attachment_data}
                    outterContainerClassName={classes.previewButtonContainer}
                  />
                )
              }
              {
                comment.content && (
                  <div className={`${classes.baloon} ${isInternalStyleClass}`}>
                    <div className={comment.attachment_data && classes.contentWithMargin}>
                      {(comment.content || '').split('\n').map((line, index) => (
                        <span key={`${Date.now()}_${index.toString()}`}>
                          {line}
                          <br />
                        </span>
                      ))}
                    </div>
                  </div>
                )
              }
              <Typography variant="body2" color="textSecondary" component="small">
                {`${moment(_get(comment, 'created_at')).calendar()}${isInternalLabel}`}
              </Typography>
            </React.Fragment>
          )
        }
      </div>
    </div>
  );
});

TicketComment.propTypes = {
  classes: PropTypes.object,
  comment: PropTypes.object
};

export default withStyles(styles, { withTheme: true })(TicketComment);
