import React, { memo } from 'react';
import { get, map, forEach } from 'lodash';
import { compose, withState, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import {
  Typography, Grid, Paper, Button
} from '@material-ui/core';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';

import ServiceService from '../../../services/service';
import ServiceRequestService from '../../../services/service-request';
import withRequest from '../../../utils/with-request';
import Loading from '../../../components/Loading';
import {
  FormTextField
} from '../../../components/FormFields';
import styles from './styles';

const NewServiceRequest = ({
  classes,
  serviceData, serviceLoading,
  formData, setFormData,
  createServiceRequestFetch, createServiceRequestLoading: isSubmitLoading
}) => {
  if (serviceLoading) {
    return (
      <Loading />
    );
  }
  const { validationErrors } = formData;
  const errors = validationErrors || {};
  const onSubmit = () => createServiceRequestFetch(true);

  const handleEnterOnKeyDown = (e) => {
    if (e.ctrlKey && e.keyCode === 13) {
      onSubmit();
    }
  };

  const { service } = serviceData;
  return (
    <React.Fragment>
      <Typography className={classes.title} variant="h4">
        { service.title }
      </Typography>
      <Grid container className={classes.subtitlesContainer}>
        <Grid item xs={6}>
          <Typography variant="subtitle1">
            <b>{ service.subtitle }</b>
          </Typography>
        </Grid>
        <Grid item xs={6} className={classes.marginLeftContainer}>
          <Typography variant="subtitle1">
            <b>Ns lidamos com tudo</b>
          </Typography>
        </Grid>
      </Grid>
      <Grid container className={classes.textContentsContainer}>
        <Grid item xs={6}>
          <Typography variant="body2">
            {service.description}
          </Typography>
        </Grid>
        <Grid item xs={6} className={classes.marginLeftContainer}>
          <ul className={classes.benefitsList}>
            {
              map(service.benefits, benefit => (
                <li key={benefit}>
                  <Typography variant="body2">{ benefit }</Typography>
                </li>
              ))
            }
          </ul>
        </Grid>
      </Grid>
      <Paper className={`${classes.container} ${classes.paper}`}>
        <Grid container item xs={12} sm={8} md={6} spacing={2}>
          <Grid item xs={12}>
            {
              map(service.questions, question => (
                <div key={question} className={classes.questionContainer}>
                  <Typography variant="subtitle1">
                    <b>{question}</b>
                  </Typography>
                  <FormTextField
                    onKeyDown={handleEnterOnKeyDown}
                    errors={errors[question]}
                    disabled={isSubmitLoading}
                    value={formData[question]}
                    onChange={(e) => setFormData({ [question]: e.target.value })}

                    multiline
                    rows={2}
                    rowsMax={2}
                  />
                </div>
              ))
            }

            <div className={classes.actions}>
              <Button
                className={classes.goBackButton}
                component={Link}
                to="/servicos"
                color="default"
                size="large"
                disabled={isSubmitLoading}
              >
                Voltar
              </Button>
              <Button
                variant="contained"
                color="primary"
                size="large"
                onClick={onSubmit}
                disabled={isSubmitLoading}
              >
                {isSubmitLoading ? 'Aguarde...' : 'Enviar'}
              </Button>
            </div>
          </Grid>
        </Grid>
      </Paper>
    </React.Fragment>
  );
};
NewServiceRequest.propTypes = {
  classes: PropTypes.object,
  serviceData: PropTypes.object,
  serviceLoading: PropTypes.bool,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  createServiceRequestFetch: PropTypes.func,
  createServiceRequestLoading: PropTypes.bool
};

const initialFormData = (props) => {
  const { serviceData } = props;
  const formData = {
    validationErrors: {}
  };
  forEach(get(serviceData, 'service.questions'), question => {
    formData[question] = '';
  });
  return formData;
};

const withService = withRequest(ServiceService.getService, {
  name: 'service',
  options: ({ match }) => ({ variables: match.params.serviceId })
});

const withCreateServiceRequest = withRequest(ServiceRequestService.createServiceRequest, {
  name: 'createServiceRequest',
  init: false,
  options: ({ serviceData, formData, setFormData, setAlert }) => {
    const { validationErrors, ...dynamicQuestions } = formData;
    return {
      variables: { serviceId: get(serviceData, 'service.id'), dynamicQuestions },
      onRequestFinished: () => {
        setAlert({
          alertMsg: 'Recebemos sua solicitao, em breve responderemos via email.',
          alertType: 'success',
          showAlert: true
        });
        setFormData(initialFormData({ serviceData }));
      },
      onRequestError: error => {
        setAlert({
          alertMsg: 'Erro ao solicitar servio', alertType: 'error', showAlert: true
        });
        setFormData({ validationErrors: get(error, 'errors', {}) });
      }
    };
  }
});

export default compose(
  memo,
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('formData', 'setFormDataRaw', initialFormData),
  withHandlers({
    setFormData: ({ setFormDataRaw, formData }) => (attrs) => {
      setFormDataRaw({ ...formData, ...attrs });
    }
  }),
  withService,
  withCreateServiceRequest
)(NewServiceRequest);
