import React, { memo } from 'react';
import {
  compose, withState, withHandlers
} from 'recompose';
import PropTypes from 'prop-types';

import withRequest from '../../../../utils/with-request';
import uploadFile from '../../../../utils/upload-files';
import AttachButton from '../../../../components/AttachButton';
import FilePreviewButton from '../../../../components/FilePreviewButton';
import TicketCommentsService from '../../../../services/ticket-comments';

const CommentAttachButtonBase = memo(({
  file, setFile, onFileChange, disabled
}) => {
  if (file && file.progressPercent) {
    return (
      <FilePreviewButton
        disabled={disabled}
        file={file}
        onRemoveFile={fileToRemove => fileToRemove.cancelUpload || setFile(null)}
      />
    );
  }

  return (
    <AttachButton
      disabled={disabled}
      accept="image/*,.doc,.docx,.xls,.xlsx,.pdf,.xml"
      onChange={onFileChange}
      buttonProps={{ style: { backgroundColor: '#fff' } }}
    />
  );
});

CommentAttachButtonBase.propTypes = {
  file: PropTypes.object,
  disabled: PropTypes.bool,
  setFile: PropTypes.func,
  onFileChange: PropTypes.func
};

const withSendTicketComment = withRequest(TicketCommentsService.sendComment, {
  name: 'sendTicketComment',
  init: false,
  options: ({ setFile, onAttachmentSent, onUploadError }) => ({
    onRequestFinished: () => {
      setFile(null);
      if (onAttachmentSent) onAttachmentSent();
    },
    onRequestError: err => {
      onUploadError(err);
    }
  })
});

const CommentAttachButton = compose(
  withState('file', 'setFile', null),
  withSendTicketComment,
  withHandlers({
    onFileChange: ({
      ticketId, isInternal, onUploadError, setFile, sendTicketCommentFetch
    }) => (newFile) => {
      uploadFile({
        file: newFile,
        progressHandler: ({ progressPercent }) => {
          if (progressPercent < 100) setFile({ ...newFile, progressPercent });
        }
      })
        .catch(e => {
          setFile(null);
          if (onUploadError) onUploadError(e);
        })
        .then(result => {
          const updatedFile = { ...newFile, ...result, progressPercent: 99 };
          setFile(updatedFile);

          sendTicketCommentFetch(true, null, null, {
            variables: {
              id: ticketId, attachmentData: updatedFile, content: null, isInternal
            }
          });
        });
    }
  })
)(CommentAttachButtonBase);

export default CommentAttachButton;
