import Api from '../utils/api';

const Locations = {
  getLocations(listInactive) {
    return Api.get('/locations', { list_inactive: !!listInactive });
  },
  createLocation(location) {
    return Api.post('/locations', location);
  },
  updateLocation({ id, changes }) {
    return Api.put(`/locations/${id}`, changes);
  },
  toggleActive(id) {
    return Api.delete(`/locations/${id}/toggle_active`);
  },
  getLocationsByCompany(companyId) {
    if (companyId) {
      return Api.get('/public_locations', { company_id: companyId });
    }
    return null;
  }
};

export default Locations;
